/* Custom styling for Mermaid diagrams */
.mermaid {
  text-align: center;
  margin: 2rem auto;
  overflow-x: auto;
  max-width: 100%;
  background-color: rgba(240, 240, 240, 0.3);
  padding: 1rem;
  border-radius: 5px;
}

/* Dark mode adjustments */
@media (prefers-color-scheme: dark) {
  .mermaid {
    background-color: rgba(40, 40, 40, 0.3);
  }
}


